import React, { useContext, useEffect, useLayoutEffect } from 'react';
import { Route, Switch, useHistory } from 'react-router-dom';
import { Registration } from '../../components/Auth/Registration/Registration';
import { GlobalContext } from '../../GlobalContext';
import { GlobalModal } from '../../components/GlobalModal/GlobalModal';
import { Auth } from '../../components/Auth/Auth';
import { Routes } from '../../services/routes.enum';
import { VerifyEmail } from '../../components/VerifyEmail/VerifyEmail';
import { ErrorPage } from '../../components/ErrorPage/ErrorPage';
import { SpinnerGlobal } from '../../common/SpinnerGlobal/SpinnerGlobal';
import { RulesService } from '../../components/RulesService/RulesService';
import { PrivacyPolicy } from '../../components/PrivacyPolicy/PrivacyPolicy';
import { AppInformation } from '../../common/AppInformation/AppInformation';
import { Profile } from '../Profile/Profile';
import { PrivateRoute } from '../../common/Routing/PrivateRoute/PrivateRoute';
import { EditUserProfile } from '../../components/EditUserProfile/EditUserProfile';
import { Alliance } from '../Alliance/Alliance';
import { LaborUnion } from '../LaborUnion/LaborUnion';
import { Icons } from '../../common/Icon/Icon.interface';
import { Security } from '../../components/Security/Security';
import { SimpleRoutingContainer } from '../../common/Routing/SimpleRoutingContainer/SimpleRoutingContainer';
import { InviteColleague } from '../../components/InviteColleague/InviteColleague';
import { Landing } from '../../components/Landing/Landing';

const { REACT_APP_ENV } = process.env;

const securityNavs: {
  id: number;
  label: string;
  icon: keyof typeof Icons;
  path: Routes;
  requireAuth?: boolean;
}[] = [
  {
    id: 1,
    label: 'безопасность',
    icon: 'fingerprint',
    path: Routes.SECURITY,
    requireAuth: true
  },
  {
    id: 2,
    label: 'правила сервиса',
    icon: 'fingerprint',
    path: Routes.RULES_SERVICE
  },
  {
    id: 3,
    label: ' политика конфиденциальности\n',
    icon: 'fingerprint',
    path: Routes.PRIVACY_POLICY
  }
];

function App() {
  const {
    globalModalOptions: { isOpen },
    spinner: { isSpinnerOpened }
  } = useContext(GlobalContext);
  const history = useHistory();

  useLayoutEffect(() => {
    const param = new URLSearchParams(history.location.search);
    if (param.has('invite_id') && param.has('invite_code')) {
      // InviteLink data -> registration process
      history.push(`${Routes.INVITATION_PAGE}${history.location.search}`);
    }

    initGoogleAnalytics();
  }, []);

  // block navigation when any modal is open
  useEffect(() => {
    let unblock;
    if (isOpen) {
      unblock = history.block();
    }

    return () => {
      return unblock && unblock();
    };
  }, [isOpen]);

  return (
    <>
      <AppInformation />
      <Switch>
        <Route exact path={Routes.HOME}>
          {/* <Redirect to={Routes.PROFILE + history.location.search} /> */}
          <Landing />
        </Route>

        <Route path={`${Routes.VERIFY_EMAIL}/:id/:hash`}>
          <VerifyEmail />
        </Route>

        <Route path={Routes.LOGIN}>
          <Auth type="login" />
        </Route>

        <Route path={Routes.INVITATION_PAGE}>
          <Auth type="invitationPage" />
        </Route>

        <Route path={Routes.FORGET_PASSWORD}>
          <Auth type="forgetPassword" />
        </Route>

        <Route path={`${Routes.RESET_PASSWORD}/:token`}>
          <Auth type="resetPassword" />
        </Route>

        <Route path={Routes.REGISTRATION}>
          <Registration />
        </Route>

        <Route path={Routes.ERROR_PAGE}>
          <ErrorPage />
        </Route>

        <PrivateRoute path={Routes.SECURITY} withSidebar={false} withFooter={false}>
          <SimpleRoutingContainer component={Security} navs={securityNavs} showCloseButton={true} closeButtonRoute={Routes.PROFILE} />
        </PrivateRoute>

        <Route path={Routes.RULES_SERVICE}>
          <SimpleRoutingContainer component={RulesService} navs={securityNavs} showCloseButton={true} closeButtonRoute={Routes.PROFILE} />
        </Route>

        <Route path={Routes.PRIVACY_POLICY}>
          <SimpleRoutingContainer component={PrivacyPolicy} navs={securityNavs} showCloseButton={true} closeButtonRoute={Routes.PROFILE} />
        </Route>

        <PrivateRoute path={Routes.PROFILE} component={Profile} />
        <PrivateRoute path={Routes.ALLIANCE} component={Alliance} />
        <PrivateRoute path={Routes.LABOR_UNION} component={LaborUnion} />
        <PrivateRoute
          path={Routes.EDIT_USER_PROFILE}
          component={EditUserProfile}
          withSidebar={false}
          withFooter={false}
          showCloseButton={true}
          closeButtonRoute={Routes.PROFILE}
        />

        <Route path={Routes.INVITE_COLLEAGUE} component={InviteColleague} />
      </Switch>
      {isOpen && <GlobalModal />}
      {/* eslint-disable-next-line react/jsx-no-undef */}
      {isSpinnerOpened && <SpinnerGlobal />}
    </>
  );
}

export default App;

function initGoogleAnalytics() {
  if (REACT_APP_ENV !== 'production') return;

  const scriptTagId = document.createElement('script');
  scriptTagId.setAttribute('src', 'https://www.googletagmanager.com/gtag/js?id=G-41R2KD99ME');
  scriptTagId.async = true;
  document.head.appendChild(scriptTagId);

  const script = document.createElement('script');
  script.innerText =
    "window.dataLayer = window.dataLayer || []; function gtag(){ dataLayer.push(arguments); } gtag('js', new Date()); gtag('config', 'G-41R2KD99ME');";

  document.head.appendChild(script);
}
