import axios, { AxiosInstance } from 'axios';
const { REACT_APP_BASE_URL, REACT_APP_API_URL } = process.env;

export enum Api {
  // BASE_URL = 'http://localhost:3000',
  // BASE_URL = 'https://app.tradeunion.online',

  //Auth
  REGISTER = '/api/auth/register',
  RESEND_EMAIL = '/api/auth/resend',
  LOGIN = '/api/auth/login',
  LOGOUT = '/api/auth/logout',
  USER = '/api/auth/user',
  INVITE_INFO = '/api/auth/invite',
  VERIFY_EMAIL = '/api/auth/verify_email',
  FORGET_PASSWORD = '/api/auth/forgot',
  RESET_PASSWORD = '/api/auth/reset_password',

  //User
  UPDATE_USER = '/api/me/update',
  UPDATE_PASSWORD = '/api/me/update_password',
  UPDATE_PUBLIC_NAME = '/api/me/new_name',
  UPDATE_AVATAR = '/api/me/update_avatar',
  UPDATE_VISIBILITY = '/api/me/change_visibility',
  INVITED = '/api/me/invited',

  //Dictionaries
  COUNTRIES = '/api/dictionaries/countries',
  ACTIVITY_SCOPES = '/api/dictionaries/activity_scopes',
  SEARCH_PLACE = '/api/dictionaries/search_place',
  HELP_OFFERS = '/api/dictionaries/help_offers',
  ORGANISATION_TYPES = '/api/dictionaries/organization_types',

  //Invite links
  CURRENT_LINK = '/api/invite_links/current',
  INVITE_LINKS = '/api/invite_links',

  // organisations
  ORGANISATIONS = '/api/organizations',
  ORGANIZATION = '/api/me/organization',
  ORGANIZATION_MEMBERS = '/api/admin_org',

  // suggestions
  SUGGESTIONS = '/api/suggestions'
}

export class HttpService {
  readonly API_URL = REACT_APP_API_URL;
  readonly BASE_URL = REACT_APP_BASE_URL;
  private readonly tokenKey = 'token';
  private _token: string = null;

  axios: AxiosInstance = null;

  constructor() {
    this._token = localStorage.getItem(this.tokenKey) || null;
    this.axios = axios.create({
      baseURL: this.API_URL,
      headers: {
        authorization: this._token ? `Bearer ${this._token}` : ''
      }
    });
  }

  set token(value: string) {
    this._token = value;
    this.axios.defaults.headers.authorization = `Bearer ${value}`;
    localStorage.setItem(this.tokenKey, this._token);
  }

  get token() {
    return localStorage.getItem(this.tokenKey);
  }

  clearToken() {
    this._token = null;
    this.axios.defaults.headers.authorization = null;
    localStorage.removeItem(this.tokenKey);
  }
}
