import React, { useEffect, useRef, useState } from 'react';
import styles from './Input.module.scss';
import { FormError } from '../Validator/FormError/FormError';
import { Spinner } from '../Spinner/Spinner';

export type IInputProps = {
  value?: string | number;
  valueChange: (value: string) => void;
  type?: 'text' | 'email' | 'password' | 'number';
  name: string;
  label?: string;
  description?: string;
  error?: string;
  onTouch?: () => void;
  autocompleteOptions?: any[];
  autocompleteOptionsLabelKey?: string;
  autocompleteOnSelect?: (value: string) => void;
  autocompleteOnCancel?: () => void;
  isAutocompleteOptionsLoading?: boolean;
  bgColor?: 'white' | 'gray';
  disabled?: boolean;
  required?: boolean;
  prefix?: string;
  placeholder?: string;
  showPassword?: boolean;
};

export function Input({
  value = '',
  type = 'text',
  valueChange,
  name,
  label,
  description,
  error,
  onTouch,
  autocompleteOptions = null,
  autocompleteOptionsLabelKey = '',
  autocompleteOnSelect,
  autocompleteOnCancel,
  isAutocompleteOptionsLoading,
  bgColor = 'gray',
  disabled,
  required = false,
  prefix = '',
  placeholder,
  showPassword: showPasswordDefaultState = false
}: IInputProps) {
  const [showPassword, setShowPasswordStatus] = useState<boolean>(showPasswordDefaultState);
  const [isFocused, setIsFocusedStatus] = useState<boolean>(false);

  const handleChange = (event) => {
    // onTouch && onTouch();
    valueChange(event.target.value);
  };

  const handleAutocompleteOnSelect = (value) => {
    autocompleteOnSelect(value);
  };

  const onFocus = () => {
    setIsFocusedStatus(true);
  };

  const onBlur = () => {
    setIsFocusedStatus(false);
    onTouch && onTouch();
  };

  const ref = useRef(null);

  const handleClickOutside = (event) => {
    if (ref.current && !ref.current.contains(event.target)) {
      autocompleteOnCancel && autocompleteOnCancel();
    }
  };

  useEffect(() => {
    if (autocompleteOptions?.length) {
      document.addEventListener('click', handleClickOutside, true);
      return () => {
        document.removeEventListener('click', handleClickOutside, true);
      };
    }
  }, [autocompleteOptions]);

  const className = `${styles.formGroup} form-group p-0 ${showPassword ? styles.openPwd : ''} ${isFocused ? 'focused' : ''} ${
    value ? 'filled' : ''
  } ${error ? 'error' : ''} ${disabled ? 'disabled' : ''}`;

  return (
    <div className={className} ref={ref}>
      <label className={styles.label} htmlFor={name}>
        {!placeholder || value ? label && label + (required ? ' *' : '') : ''}
      </label>
      <div
        className={`${styles.inputContainer} ${prefix && (isFocused || value) ? styles.withPrefix : ''} ${
          bgColor === 'gray' ? styles.gray : styles.white
        } ${disabled ? styles.disabled : ''}`}
      >
        {placeholder && !value && <div className={styles.placeholder}>{placeholder}</div>}
        {prefix && (isFocused || value) && (
          <div className={`${styles.prefix} prefix`}>
            <span>{prefix}</span>
          </div>
        )}
        <input
          className={``}
          id={name}
          name={name}
          type={showPassword ? 'text' : type}
          value={value}
          onChange={handleChange}
          onFocus={onFocus}
          onBlur={onBlur}
          disabled={disabled}
        />
        {type === 'password' && <div className={styles.showPwd} onClick={setShowPasswordStatus.bind(this, !showPassword)} />}
        {isAutocompleteOptionsLoading && (
          <div className={styles.spinnerContainer}>
            <Spinner size={20} />
          </div>
        )}
      </div>

      <div className={`${styles.description} description`}>{error ? <FormError>{error}</FormError> : !value && description}</div>
      {autocompleteOptions && (
        <div className={`select__options custom-scroll custom-scroll-black ${styles.selectOptions}`}>
          <div className="select__scroll">
            {autocompleteOptions?.map((option) => (
              <div
                className={`select__option`}
                onClick={handleAutocompleteOnSelect.bind(this, option[autocompleteOptionsLabelKey])}
                key={option[autocompleteOptionsLabelKey]}
              >
                {option[autocompleteOptionsLabelKey]}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
