import React, { useState } from 'react';
import './Checkbox.scss';

type ICheckboxProps = {
  value?: boolean;
  valueChange: (value: boolean) => void;
  name: string;
  label?: any;
  error?: string;
  onTouch?: () => void;
};

export function Checkbox({ value, valueChange, label, name, error, onTouch }: ICheckboxProps) {
  const [inputValue, setValue] = useState<boolean>(value);

  const handleChange = (event) => {
    valueChange(event.target.checked);
    setValue(event.target.checked);
    onTouch && onTouch();
  };

  const className = `form-group__checkbox ${inputValue ? 'filled' : ''} ${error ? 'error' : ''}`;

  return (
    <div className={className}>
      <label htmlFor={name}>
        <input className={value ? 'checked' : ''} id={name} name={name} type="checkbox" checked={inputValue} onChange={handleChange} />
        <span>{label}</span>
      </label>
    </div>
  );
}
