import React, { useContext, useLayoutEffect } from 'react';
import styles from './SimpleRoutingContainer.module.scss';
import { useHistory } from 'react-router-dom';
import { Icon } from '../../Icon/Icon';
import { Footer } from '../../../components/Footer/Footer';
import { Icons } from '../../Icon/Icon.interface';
import { Routes } from '../../../services/routes.enum';
import { CloseButton } from '../../CloseButton/CloseButton';
import { GlobalContext } from '../../../GlobalContext';
import { Sidebar } from '../../../components/Sidebar/Sidebar';

type ISimpleRoutingContainerProps = {
  navs?: {
    id: number;
    label: string;
    icon: keyof typeof Icons;
    path: Routes;
    requireAuth?: boolean;
  }[];
  component: any;
  showCloseButton?: boolean;
  closeButtonRoute?: Routes;
};

export function SimpleRoutingContainer({ navs, component, showCloseButton = false, closeButtonRoute }: ISimpleRoutingContainerProps) {
  const {
    services: { userService },
    isSidebarExpanded,
    setSidebarExpandedState
  } = useContext(GlobalContext);
  const history = useHistory();

  useLayoutEffect(() => {
    return () => {
      setSidebarExpandedState(false);
    };
  }, []);

  return (
    <div className={`${styles.SimpleRoutingContainer}`}>
      <div className={styles.closeButton}>{showCloseButton && <CloseButton targetRoute={closeButtonRoute} />}</div>
      <nav className=" p-left p-right">
        <div className={`${styles.logo} p-top p-bottom`}>
          <Icon iconName="logoSmall" />
        </div>

        <ul className={styles.navbar}>
          {navs
            .filter((navItem) => userService.isLoggedIn || !navItem.requireAuth)
            .map((navItem) => (
              <li
                className={navItem.path === history.location.pathname ? styles.active : ''}
                onClick={() => {
                  if (history.location.pathname !== navItem.path) {
                    history.push(navItem.path);
                  }
                }}
                key={navItem.id}
              >
                <span>{navItem.label}</span>
                {navItem?.icon && <Icon iconName={navItem.icon} />}
              </li>
            ))}
        </ul>

        <div className={`${styles.menuIcon}`} onClick={setSidebarExpandedState.bind(null, !isSidebarExpanded)}>
          <Icon iconName="menu" />
        </div>
      </nav>
      {React.createElement(component)}
      <Sidebar isExpanded={isSidebarExpanded} setExpandedState={setSidebarExpandedState} mobileOnly={true} />
      <div className="p">
        <Footer showLinks={false} />
      </div>
    </div>
  );
}
