import React from 'react';
import styles from './Topbar.module.scss';
import { Navbar } from './Navbar/Navbar';
import { Icon } from '../../common/Icon/Icon';
import { GlobalContext } from '../../GlobalContext';
import { TopbarProfileImage } from './TopbarProfileImage/TopbarProfileImage';
import { CustomImage } from '../../common/CustomImage/CustomImage';
import { useHistory } from 'react-router-dom';
import { Routes } from '../../services/routes.enum';

type ITopbarProps = {
  imageSrc?: string;
  imageAlt?: string;
  leftContainer?: React.ReactElement;
  rightContainer?: React.ReactElement;
};

export function Topbar({ imageSrc, imageAlt, leftContainer = null, rightContainer = null }: ITopbarProps) {
  const { isSidebarExpanded, setSidebarExpandedState, user, selectedOrganisation } = React.useContext(GlobalContext);
  const history = useHistory();

  return (
    <div className={`${styles.topbar} p-left-xxl p-right-md`}>
      <div
        className={`${styles.image}  p-left p-0-left-xxl-only p-right-xs-only p-right-sm-only p-top p-0-top-xxl p-bottom p-0-bottom-xxl`}
      >
        {history.location.pathname === Routes.ALLIANCE && (
          <CustomImage src={selectedOrganisation?.avatar} alt={selectedOrganisation?.short_name} />
        )}
        {history.location.pathname === Routes.PROFILE && <TopbarProfileImage user={user} />}
        <div className={`${styles.menuIcon}`} onClick={setSidebarExpandedState.bind(null, !isSidebarExpanded)}>
          <Icon iconName="menu" />
        </div>
      </div>
      <div className={`${styles.leftContainer} p-top p-0-top-lg`}>{leftContainer}</div>
      <div className={styles.navbar}>
        <Navbar />
      </div>
      <div className={`${styles.rightContainer} p-top p-0-top-xl`}>{rightContainer}</div>
    </div>
  );
}
