import React from 'react';
import './Button.scss';
import { Icons } from '../Icon/Icon.interface';
import { Icon } from '../Icon/Icon';

export type IButtonProps = {
  onClick?: (...params: any[]) => void;
  children?: any;
  disabled?: boolean;
  primary?: boolean;
  icon?: keyof typeof Icons;
  iconPosition?: 'left' | 'right';
  type?: 'button' | 'submit' | 'reset';
  textUpperCase?: boolean;
};

export function Button({
  onClick,
  primary = false,
  disabled = false,
  icon,
  iconPosition = 'right',
  children,
  type = 'button',
  textUpperCase = true
}: IButtonProps) {
  const className = `${primary ? 'primary' : ''} ${icon ? 'with-icon' : ''} ${iconPosition === 'right' ? 'icon-right' : 'icon-left'}`;

  return (
    <button className={className + (textUpperCase ? ' text-uppercase' : '')} disabled={disabled} onClick={onClick} type={type}>
      {children}
      {icon ? <Icon iconName={icon} /> : null}
    </button>
  );
}
