import React, { useEffect, useLayoutEffect, useState } from 'react';
import styles from './Profile.module.scss';
import { LaborUnionInfo } from '../../common/LaborUnioniInfo/LaborUnionInfo';
import { Tabs } from '../../common/Tabs/Tabs';
import { TabItem } from '../../common/Tabs/TabItem';
import { InDeveloping } from '../../common/InDeveloping/InDeveloping';
import { TopSuggestions } from '../../components/Suggestions/TopSuggestions/TopSuggestions';
import { SuggestionForm } from '../../components/Suggestions/SuggestionForm/SuggestionForm';
import { GlobalContext } from '../../GlobalContext';
import { Topbar } from '../../components/Topbar/Topbar';
import { SelectWithButton } from '../../common/SelectWithButton/SelectWithButton';
import { Button } from '../../common/Button/Button';
import { NextSteps } from './NextSteps/NextSteps';
import { IHelpOffer } from '../../services/help-offer.interface';
import { dictionariesService } from '../../services';
import { Routes } from '../../services/routes.enum';
import { useHistory } from 'react-router-dom';

// type IProfileProps = {};

export function Profile() {
  const {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    user,
    selectedOrganisation,
    applyToOrganizationIconRef,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    services: { suggestionsService, organisationsService },
    spinner: { showSpinner, hideSpinner }
  } = React.useContext(GlobalContext);
  const [suggestionBlockIsActive, setSuggestionBlockStatus] = useState<boolean>(false);
  const [applyToOrganizationIsDisplayed, setApplyToOrganizationState] = useState<boolean>(false);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [suggestions, setSuggestions] = useState<any>(null);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isMembership, setIsMembershipStatus] = useState<boolean>(false);
  const [helpOffers, setHelpOffers] = useState<IHelpOffer[]>(null);
  const history = useHistory();

  const toggleApplyToOrganization = () => {
    setApplyToOrganizationState(!applyToOrganizationIsDisplayed);
  };
  useEffect(() => {
    applyToOrganizationIconRef?.current && applyToOrganizationIconRef.current.addEventListener('click', toggleApplyToOrganization);

    (async () => {
      const helpOffers = await dictionariesService.getHelpOffers();
      if (helpOffers) {
        setHelpOffers(helpOffers);
      }
    })();

    return () => {
      // TODO check for memory leak
      applyToOrganizationIconRef?.current?.removeEventListener('click', toggleApplyToOrganization);
    };
  }, []);

  // TODO return when work with organisations and suggestion wil be available
  useLayoutEffect(() => {
    (async () => {
      if (selectedOrganisation && user.membership.find((org) => org.id === selectedOrganisation.id)) {
        showSpinner();
        const suggestions = await suggestionsService.getSuggestions(selectedOrganisation.id);
        setIsMembershipStatus(true);
        setSuggestions(suggestions);
        hideSpinner();
      } else {
        setIsMembershipStatus(false);
        setSuggestions(null);
      }
    })();
  }, [selectedOrganisation]);

  const onSelect = (event: any) => {
    console.log(event);
  };

  const leftContainer = (
    <SelectWithButton
      selectProps={{
        options: helpOffers,
        value: null,
        labelKey: 'text',
        label: 'Как вы готовы помочь?',
        onSelect,
        multiselect: true
      }}
      buttonProps={{ primary: true, children: 'Готов' }}
    />
  );

  const openInviteColleagueModal = () => {
    history.push(Routes.INVITE_COLLEAGUE + history.location.search);
  };

  return (
    <>
      <Topbar
        leftContainer={leftContainer}
        rightContainer={
          <div className="form-group p-left p-0-left-lg p-right p-0-right-lg">
            <Button icon="group" onClick={openInviteColleagueModal}>
              Пригласить друга
            </Button>
          </div>
        }
      />
      <div className={`${styles.Profile}`}>
        <div className="p-left p-right">
          {/*{applyToOrganizationIsDisplayed && <ApplyToOrganization/>}*/}
          <h2>предложения Объединения</h2>
          {selectedOrganisation && <LaborUnionInfo organisation={selectedOrganisation} />}
          {/*{isMembership && (*/}
        </div>

        <div className={styles.tabsContainer}>
          {suggestionBlockIsActive ? (
            <div className="p-left p-right">
              <SuggestionForm setSuggestionBlockStatus={setSuggestionBlockStatus} />
            </div>
          ) : (
            <div className="p-left-md p-right-md">
              <Tabs>
                <TabItem label="Новые">
                  <TopSuggestions setSuggestionBlockStatus={setSuggestionBlockStatus} />
                </TabItem>
                <TabItem label="Мои" disabled={true}>
                  <InDeveloping />
                </TabItem>
                <TabItem label="Все" disabled={true}>
                  <InDeveloping />
                </TabItem>
              </Tabs>
            </div>
          )}
        </div>
        {/*)}*/}
      </div>
      <NextSteps type="profile" />
    </>
  );
}
