import React, { useContext, useEffect, useState } from 'react';
import styles from './Alliance.module.scss';
import { Topbar } from '../../components/Topbar/Topbar';
import { Button } from '../../common/Button/Button';
import { GlobalContext } from '../../GlobalContext';
import { IOrganisation } from '../../services/organisation.interface';
import { IUser } from '../../services/user.interface';
import { NextSteps } from '../Profile/NextSteps/NextSteps';
import { Slider } from '../../common/Slider/Slider';
import { CustomImage } from '../../common/CustomImage/CustomImage';
import { Icon } from '../../common/Icon/Icon';

export function Alliance() {
  const {
    user,
    selectedOrganisation,
    services: { organisationsService },
    spinner: { showSpinner, hideSpinner }
  } = useContext(GlobalContext);
  const [userInOrganisation, setUserInOrganisationStatus] = useState<boolean>(null);
  const [organisation, setOrganisation] = useState<IOrganisation>();

  const getOrganisation = async (organisationId: number) => {
    showSpinner();
    const organisation = await organisationsService.getOrganisationById(organisationId);
    setOrganisation(organisation);
    hideSpinner();
  };

  const checkIfUserInOrganisation = (user: IUser, organisationId: number): boolean => {
    return !!user?.membership.find((organisation) => organisation.id === organisationId) || false;
  };

  useEffect(() => {
    if (selectedOrganisation) {
      getOrganisation(selectedOrganisation.id);
      setUserInOrganisationStatus(checkIfUserInOrganisation(user, selectedOrganisation.id));
    }
  }, [selectedOrganisation]);

  const onEnterOrganisation = async () => {
    showSpinner();
    const result = await organisationsService.enterOrganisation(selectedOrganisation.id);
    if (result.ok) {
      setUserInOrganisationStatus(true);
    }
    hideSpinner();
  };
  const onLeaveOrganisation = async () => {
    showSpinner();
    const result = await organisationsService.leaveOrganisation(selectedOrganisation.id);
    if (result.ok) {
      setUserInOrganisationStatus(false);
    }
    hideSpinner();
  };

  const leftContainer = (
    <>
      {userInOrganisation ? (
        <Button>создать объединение</Button>
      ) : (
        <Button primary={true} onClick={onEnterOrganisation}>
          вступить
        </Button>
      )}
    </>
  );

  const openInviteColleagueModal = () => {};

  return (
    <div className={`${styles.Alliance}`}>
      <div className="p-left p-right">
        <Topbar
          leftContainer={leftContainer}
          rightContainer={
            <div className="form-group">
              <Button icon="group" onClick={openInviteColleagueModal}>
                Пригласить друга
              </Button>
            </div>
          }
        />
        <h2>
          {organisation?.type_name} {organisation?.short_name}
        </h2>
        <div className={styles.organisationInfo}>
          <div className="text-uppercase text-button">{organisation?.name}</div>
          <div className={styles.organisationDetails}>
            <div className="text-uppercase text-button">деятельность:</div>
            <div>Машиностроение</div>
            <div className="text-uppercase text-button">Пользователи:</div>
            <div>
              547241 <span>+43</span> сегодня
            </div>
            <div className="text-uppercase text-button">Статус:</div>
            <div>
              Официально зарегистрирована
              <br />
              В процессе регистрации
              <br />
              Без регистрации
            </div>
          </div>
          <div>
            {organisation?.description ||
              'Группа «Атлант» включает помимо двух производств в Минске (холодильники и стиральные машины), Барановичский станкостроительный завод, молодечненский производитель комплектующих «Белхол» и сельхозпредприятие. Работает с чистым убытком'}
          </div>
        </div>

        <h2>структура объединения</h2>
        <div className={styles.card} />

        <h2>участники объединения</h2>

        <div className={styles.card}>
          <Slider
            controlsVerticalOffset={70}
            children={[1, 2, 3].map((item) => (
              <UserCard key={item} user={user} />
            ))}
          />
        </div>
      </div>

      <NextSteps type="alliance" />

      <div className="p-left p-right">
        {userInOrganisation && (
          <Button primary={true} onClick={onLeaveOrganisation}>
            выйти из объединения
          </Button>
        )}
      </div>
    </div>
  );
}

type IUserCartProps = {
  user: IUser;
};

function UserCard({ user }: IUserCartProps) {
  return (
    <div className={`${styles.userCard} p-bottom`}>
      <div>
        <CustomImage src={user?.public_avatar} alt="Avatar" />
      </div>
      <div>
        <h3>
          {user?.public_family} {user?.public_name}
        </h3>
        <hr />
        <p>
          <strong>Должность / статус:</strong> Председатель
        </p>
        <br />
        <p>
          Можно. Уставы всех четырех независимых профсоюзов Беларуси разрешают членство в нескольких профсоюзах. А вот Федерация профсоюзов
        </p>
        <br />
        <div className={styles.social}>
          <Icon iconName="telegram" />
          <Icon iconName="email" />
        </div>
      </div>
    </div>
  );
}
