import React, { useState } from 'react';
import styles from './CustomDatePicker.module.scss';
import { FormError } from '../Validator/FormError/FormError';
import 'react-datepicker/dist/react-datepicker.css';
import DatePicker from 'react-datepicker';
import moment from 'moment';
import { Button } from '../Button/Button';
import { Select } from '../Select/Select';
import { registerLocale } from 'react-datepicker';
import ru from 'date-fns/locale/ru';
import { Icon } from '../Icon/Icon';

registerLocale('ru', ru);

export type IInputProps = {
  value?: string | Date;
  valueChange: (value: string) => void;
  name: string;
  label?: string;
  description?: string;
  error?: string;
  onTouch?: () => void;
  bgColor?: 'white' | 'gray';
  disabled?: boolean;
  required?: boolean;
  minDate?: any; // YYYY-MM-DD
  maxDate?: any; // YYYY-MM-DD
};

export function CustomDatePicker({
  value = '',
  valueChange,
  name,
  label,
  description,
  error,
  onTouch,
  bgColor = 'gray',
  disabled,
  required = false,
  minDate,
  maxDate
}: IInputProps) {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isFocused, setIsFocusedStatus] = useState<boolean>(false);

  const maxYear = moment.utc(maxDate).year();
  const minYear = moment.utc(minDate).year();
  const years = Array(maxYear - minYear)
    .fill(null)
    .map((year, index) => ({ year: maxYear - index }));

  const months = moment.months().map((monthName, index) => ({ id: index, name: monthName }));

  const handleChange = (event) => {
    onTouch && onTouch();
    valueChange(event.target.value);
  };

  const className = `form-group ${isFocused ? 'focused' : ''} ${value ? 'filled' : ''} ${error ? 'error' : ''} ${
    disabled ? 'disabled' : ''
  }`;

  return (
    <div className={className}>
      <label htmlFor={name}>{label && label + (required ? ' *' : '')}</label>
      <div className={`${styles.inputContainer} ${isFocused || value ? styles.withPrefix : ''}`}>
        <DatePicker
          locale="ru"
          calendarStartDay={1}
          selected={value}
          onChange={(date) => {
            handleChange({ target: { value: date } });
          }}
          dateFormat="dd-MM-yyyy"
          minDate={minDate}
          maxDate={maxDate}
          renderCustomHeader={({
            date,
            changeYear,
            changeMonth,
            decreaseMonth,
            increaseMonth,
            prevMonthButtonDisabled,
            nextMonthButtonDisabled
          }) => (
            <div className={styles.customControl}>
              <div className={`${styles.previousMonth} form-group`}>
                <Button primary={true} onClick={decreaseMonth} disabled={prevMonthButtonDisabled}>
                  <Icon iconName="arrowLeft" color="white" width={8} />
                </Button>
              </div>
              <div className={styles.year}>
                <Select
                  label="Год"
                  options={years}
                  value={moment(date).year()}
                  idKey="year"
                  labelKey="year"
                  onSelect={(newValue) => {
                    changeYear(newValue);
                  }}
                />
              </div>

              <div className={styles.month}>
                <Select
                  label="Месяц"
                  options={months}
                  value={moment(date).month()}
                  idKey="id"
                  labelKey="name"
                  onSelect={(newValue) => {
                    changeMonth(newValue);
                  }}
                />
              </div>

              <div className={`${styles.nextMonth} form-group`}>
                <Button primary={true} onClick={increaseMonth} disabled={nextMonthButtonDisabled}>
                  <Icon iconName="arrowRight" color="white" width={8} />
                </Button>
              </div>
            </div>
          )}
        />
      </div>
      <div className="description">{error ? <FormError>{error}</FormError> : !value && description}</div>
    </div>
  );
}
