import React from 'react';
import telegramIcon from '../../../public/telegram.svg';
import { IUser } from '../../../services/user.interface';
import './TopbarProfileImage.scss';
import { CustomImage } from '../../../common/CustomImage/CustomImage';

type ITopbarProfileImageProps = {
  user: IUser;
  displayTelegramIcon?: boolean;
};

export function TopbarProfileImage({ user, displayTelegramIcon = true }: ITopbarProfileImageProps) {
  return (
    <div className="TopbarProfileImage">
      <div className="image">
        {user?.public_avatar && <CustomImage src={user?.public_avatar} alt="Avatar" height={'auto'} />}
        {displayTelegramIcon && <img className="social" src={telegramIcon} alt="" />}
      </div>

      {user && (
        <div className="about">
          <h3>
            {user.public_name} {user.public_family}
          </h3>
          <div>О себе: {user.about}</div>
        </div>
      )}
    </div>
  );
}
