import React, { useContext, useLayoutEffect, useState } from 'react';
import styles from './PrivateRoute.module.scss';
import { RouteProps } from 'react-router';
import { GlobalContext } from '../../../GlobalContext';
import { Sidebar } from '../../../components/Sidebar/Sidebar';
import { Redirect, Route, useHistory } from 'react-router-dom';
import { Routes } from '../../../services/routes.enum';
import { Footer } from '../../../components/Footer/Footer';
import { CloseButton } from '../../CloseButton/CloseButton';

type IPrivateRouteProps = RouteProps & {
  component?: any;
  children?: any;
  withSidebar?: boolean;
  withFooter?: boolean;
  showCloseButton?: boolean;
  closeButtonRoute?: Routes;
};

export function PrivateRoute({
  component: Component,
  children,
  withSidebar = true,
  withFooter = true,
  showCloseButton = false,
  closeButtonRoute,
  ...rest
}: IPrivateRouteProps) {
  const {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    services: { userService, authService, organisationsService },
    user,
    setUser,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    organisations,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    setOrganisations,
    selectOrganisation,
    spinner: { showSpinner, hideSpinner },
    isSidebarExpanded,
    setSidebarExpandedState
  } = useContext(GlobalContext);
  const isLoggedIn = userService.isLoggedIn;
  const [isUserLoading, setIsUserLoadingState] = useState<boolean>(true);
  const history = useHistory();

  useLayoutEffect(() => {
    (async () => {
      if (isLoggedIn) {
        if (!user) {
          showSpinner();
          const loadedUser = await authService.getUser();
          if (!loadedUser.errors) {
            setUser(loadedUser);
            // setIsUserLoadingState(false);
          } else {
            // token is expired or inactive
            history.push(Routes.LOGIN);
          }
        }

        // TODO return when work with organisations and suggestion wil be available
        if (!organisations) {
          const loadedOrganisations = await organisationsService.getOrganisations();
          if (loadedOrganisations.length) {
            setOrganisations(loadedOrganisations);
            selectOrganisation(loadedOrganisations[0].id);
          }
        }
      }
      hideSpinner();
      setIsUserLoadingState(false);
    })();
  }, []);
  return (
    <div className={`${styles.MainContainer} ${withSidebar ? styles.withSidebar : ''} custom-scroll`}>
      {!isUserLoading && (
        <div className={styles.Main}>
          {showCloseButton && <CloseButton targetRoute={closeButtonRoute} />}
          {withSidebar && <Sidebar isExpanded={isSidebarExpanded} setExpandedState={setSidebarExpandedState} />}
          <Route
            {...rest}
            render={(props) =>
              isLoggedIn ? (Component && <Component {...props} />) || (children && children) : <Redirect to={Routes.LOGIN} />
            }
          />
          {withFooter && (
            <div className="p">
              <Footer />
            </div>
          )}
        </div>
      )}
    </div>
  );
}
