import React, { useContext, useEffect, useState } from 'react';
import styles from './StepConfirmEmail.module.scss';
import { Icon } from '../../../../common/Icon/Icon';
import { Button } from '../../../../common/Button/Button';
import { GlobalContext } from '../../../../GlobalContext';
import { InputWithButton } from '../../../../common/InputWithButton/InputWithButton';
import { ValidatorsTypes } from '../../../../common/Validator/ValidatorsTextTemplates';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { useHistory } from 'react-router-dom';

export function StepConfirmEmail() {
  const {
    services: { authService },
    spinner: { showSpinner, hideSpinner }
  } = useContext(GlobalContext);

  const history = useHistory();
  useEffect(() => {
    const unblock = history.block();
    return () => {
      unblock();
    };
  }, []);
  const [asyncErrors, setAsyncErrors] = useState<{ [key: string]: string }>({});

  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .trim()
      .email(ValidatorsTypes.invalidEmail)
      .max(100)
      .not([authService.registrationUserData.email], ValidatorsTypes.notUniqEmail)
  });

  const { errors, touched, values, handleSubmit, setFieldValue, isValid, validateForm, setFieldTouched } = useFormik({
    initialValues: { email: '' },
    validationSchema,
    validate: () => {
      return asyncErrors;
    },
    onSubmit: async (values, actions) => {
      showSpinner();
      const result = await authService.resendEmail({
        login: authService.registrationUserData.login,
        email: values.email
      });
      if (result.ok) {
        authService.registrationUserData = { email: values.email };
      } else {
        actions.setErrors({ email: result.errors.join() });
      }
      hideSpinner();
    }
  });

  const confirmEmail = async () => {
    showSpinner();
    const { login, email } = authService.registrationUserData;
    await authService.resendEmail({ login, email });
    hideSpinner();
  };

  return (
    <div className={styles.StepConfirmEmail}>
      <div className={styles.content}>
        <div className={styles.icon}>
          <Icon iconName={'logoSmall'} width={200} height={100} />
        </div>
        <p>
          Вам на почту <span className="highlight">{authService.registrationUserData?.email} </span> отправлено письмо. Проверьте почту и
          перейдите по ссылке для подтверждения е-мейла. Если письмо не пришло, проверьте папку спам или повторите отправку, либо введите
          другой е-мейл адрес
        </p>
      </div>

      <div className={`${styles.actions}`}>
        <>
          <InputWithButton
            inputProps={{
              name: 'email',
              value: values.email,
              valueChange: (value) => {
                delete asyncErrors.email;
                validateForm();
                setFieldValue('email', value, true);
                setAsyncErrors({ ...asyncErrors });
              },
              label: 'Изменить адрес электронной почты',
              description: '',
              type: 'email',
              onTouch: () => {
                setFieldTouched('email');
              },
              error: errors?.email && touched?.email && errors.email
            }}
            buttonProps={{
              primary: true,
              children: 'отправить',
              onClick: () => validateForm().then(() => handleSubmit()),
              type: 'submit',
              disabled: !isValid || !values.email.trim()
            }}
          />
        </>
        <div className="form-group">
          <Button primary={false} onClick={confirmEmail}>
            отправить повторно
          </Button>
        </div>
      </div>
    </div>
  );
}
