import React, { useEffect, useState } from 'react';
import styles from './CustomImage.module.scss';
import { Icon } from '../Icon/Icon';
import { Icons } from '../Icon/Icon.interface';

type IImageProps = {
  src: string | ArrayBuffer;
  alt: string;
  rounded?: boolean;
  width?: number;
  height?: number | string;
  background?: 'white' | 'gray';
  children?: any;
  errorImage?: keyof typeof Icons;
};

export function CustomImage({
  src = '',
  alt,
  width = 160,
  height = 160,
  rounded = true,
  background = 'gray',
  children,
  errorImage = 'profile'
}: IImageProps) {
  const [loadingError, serLoadingErrorState] = useState<boolean>(!src);
  useEffect(() => {
    serLoadingErrorState(false);
  }, [src]);

  const handleError = () => {
    serLoadingErrorState(true);
  };

  return (
    <div
      className={`${styles.CustomImage} ${rounded ? styles.round : ''} ${background === 'gray' ? styles.bgGray : styles.bgWhite}`}
      style={{ width, height }}
    >
      {loadingError ? (
        children ? (
          children
        ) : (
          <Icon iconName={errorImage} width={'50%'} height={'50%'} color="disabled" />
        )
      ) : (
        <img className={`${rounded ? styles.round : ''}`} src={src as string} alt={alt} onErrorCapture={handleError} />
      )}
    </div>
  );
}
