import React, { useContext, useState } from 'react';
import styles from './EditUserPublicName.module.scss';
import { InputWithButton } from '../../../common/InputWithButton/InputWithButton';
import { GlobalContext } from '../../../GlobalContext';

type IEditUserPublicNameProps = {
  disabled?: boolean;
};

export function EditUserPublicName({ disabled = false }: IEditUserPublicNameProps) {
  const {
    user = { public_family: '', public_name: '' },
    setUser,
    services: { userService },
    spinner: { showSpinner, hideSpinner }
  } = useContext(GlobalContext);

  const [publicName, setPublicName] = useState<string>(user ? user?.public_family + ' ' + user?.public_name : '');

  const generatePublicName = async () => {
    if (disabled) {
      return;
    }

    showSpinner();

    const { public_name, public_family } = await userService.updatePublicName();
    // @ts-ignore
    setUser({ ...user, public_name, public_family });
    setPublicName(`${public_family} ${public_name}`);

    hideSpinner();
  };

  return (
    <div className={styles.EditUserPublicName}>
      <h3>имя пользователя</h3>
      <p>
        У вас есть множество попыток генерирования имени, но как только вы нажмете кнопку "Сохранить изменения", вы не сможете поменять имя.
      </p>

      <div>
        <InputWithButton
          inputProps={{
            name: 'publicName',
            value: publicName,
            valueChange: setPublicName,
            bgColor: 'white',
            disabled: true
          }}
          buttonProps={{
            primary: true,
            children: 'генерировать',
            onClick: generatePublicName
          }}
        />
      </div>
    </div>
  );
}
