import { Api, HttpService } from './http.service';

export class SuggestionsService {
  constructor(private httpService: HttpService) {}

  async getSuggestions(organisationId: number): Promise<any> {
    try {
      const response = await this.httpService.axios.get<any>(`${Api.ORGANIZATION}/${organisationId}/suggestions`);
      return response.data.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async createSuggestion(organisationId: number, suggestion: any): Promise<any> {
    try {
      const response = await this.httpService.axios.post<any>(`${Api.ORGANIZATION}/${organisationId}/suggestions`, suggestion);
      return response.data.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async vote(suggestionId: number): Promise<any> {
    try {
      const response = await this.httpService.axios.post<any>(`${Api.SUGGESTIONS}/${suggestionId}/vote`, null);
      return response.data.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async unvote(suggestionId: number): Promise<any> {
    try {
      const response = await this.httpService.axios.post<any>(`${Api.SUGGESTIONS}/${suggestionId}/unvote`, null);
      return response.data.data;
    } catch (e) {
      return e.response.data;
    }
  }
}
