import React, { useContext, useState } from 'react';
import styles from './InviteColleague.module.scss';
import { Button } from '../../common/Button/Button';
import { Icon } from '../../common/Icon/Icon';
import { GlobalContext } from '../../GlobalContext';
import { CloseButton } from '../../common/CloseButton/CloseButton';
import { Routes } from '../../services/routes.enum';
import { Footer } from '../Footer/Footer';

export function InviteColleague() {
  const {
    services: { inviteLinksService },
    spinner: { showSpinner, hideSpinner }
  } = useContext(GlobalContext);
  const [linkWasGenerated, setLinkWasGeneratedStatus] = useState<boolean>(false);
  const [linkWasCopied, setLinkWasCopiedStatus] = useState<boolean>(false);
  const [inviteUrl, setInviteUrl] = useState<string>();
  const [isLimitExceeded, setIsLimitExceededStatus] = useState<boolean>(false);

  const showCopyLinkButton = !!navigator?.clipboard?.writeText;

  const onGenerateLink = async () => {
    showSpinner();
    const result = await inviteLinksService.getCurrentLink();
    if (result.invites === 0) {
      setIsLimitExceededStatus(true);
      hideSpinner();
      return;
    }
    const { id, code } = result;
    const link = inviteLinksService.generateInviteLink(id, code);
    setInviteUrl(link);

    if (!linkWasGenerated) {
      setLinkWasGeneratedStatus(true);
    }
    hideSpinner();
  };

  const onCopyLink = async (link) => {
    await navigator?.clipboard?.writeText(link);
    setLinkWasCopiedStatus(true);
  };

  return (
    <div className={`${styles.InviteColleague} p p-large`}>
      <CloseButton targetRoute={Routes.PROFILE} />
      <div className={styles.logo}>
        <Icon iconName="logoSmall" width={138} height={92} />
      </div>
      <div className={styles.header}>
        <h2>отправьте ссылку-приглашение человеку, которому вы доверяете</h2>
      </div>
      <div className={styles.content}>
        {isLimitExceeded && (
          <p>
            Вам доступно два приглашения в сутки.
            <br />
            Ваш лимит ссылок на сегодня исчерпан, попробуйте завтра.
          </p>
        )}

        {!isLimitExceeded &&
          (linkWasGenerated ? (
            <div className={styles.linkCopied}>
              <br />
              <div className={styles.info}>
                <h3>Приглашение действительно 24 часа. Вам доступно два приглашения в сутки.</h3>
              </div>
              <br />
              <div className={styles.link}>
                <span className="highlight">{inviteUrl}</span>
              </div>
              <br />
              <div>{linkWasCopied && <span>Ссылка-приглашение успешно скопирована в буфер обмена.</span>}</div>
            </div>
          ) : (
            <>
              <div className={`${styles.card} p`}>
                <div>
                  <div className={styles.icon}>
                    <Icon iconName="bell" width={31} height={34} />
                  </div>
                  <div className={styles.info}>
                    <h3>
                      На странице “Безопасность” этот человек будет отмечен как ваше доверенное лицо. Вам доступно два приглашения в сутки,
                      администратор может пригласить на платформу (в объединение) до 10 человек в сутки.
                    </h3>
                  </div>
                </div>
                <div>
                  <div className={styles.icon}>
                    <Icon iconName="group" width={53} height={38} />
                  </div>
                  <div className={styles.info}>
                    <h3>
                      на платформу попадают лишь доверенные люди - те, за кого поручился кто-то. если вдруг внутри оказывается нехороший
                      человек, можно отследить, кто пригласил его в систему и увидеть целую ветку неблагонадежных людей.
                    </h3>
                  </div>
                </div>
              </div>
            </>
          ))}
      </div>
      <div className={styles.actions}>
        <Footer showLogoDesktop={false} showLogoMobile={false}>
          {linkWasGenerated ? (
            <>
              {showCopyLinkButton && (
                <Button primary={!linkWasCopied} icon="group" onClick={onCopyLink.bind(null, inviteUrl)}>
                  скопировать ссылку {linkWasCopied ? 'повторно' : ''}
                </Button>
              )}
            </>
          ) : (
            <>
              <Button primary={true} icon="group" onClick={onGenerateLink} disabled={isLimitExceeded}>
                генерировать ссылку-приглашение
              </Button>
            </>
          )}
        </Footer>
      </div>
    </div>
  );
}
