export class ScrollService {
  scrollNext(
    containerClassName: string,
    scrollItemClassName: string,
    direction: 'horizontal' | 'vertical' = 'horizontal'
  ): { isLast: boolean } {
    const container = document.getElementsByClassName(containerClassName)[0];
    const containerBoundingClientRect = container.getBoundingClientRect();
    const items = Array.from(document.getElementsByClassName(scrollItemClassName));

    const index = items.findIndex((item) => {
      if (direction === 'horizontal') {
        return item.getBoundingClientRect().right > containerBoundingClientRect.right;
      }
      return item.getBoundingClientRect().bottom > containerBoundingClientRect.bottom;
    });

    if (index > -1) {
      items[index].scrollIntoView({ inline: 'end', block: 'end', behavior: 'smooth' });

      return { isLast: index === items.length - 1 };
    }
    return null;
  }

  scrollPrev(
    containerClassName: string,
    scrollItemClassName: string,
    direction: 'horizontal' | 'vertical' = 'horizontal'
  ): { isLast: boolean } {
    const container = document.getElementsByClassName(containerClassName)[0];
    const containerBoundingClientRect = container.getBoundingClientRect();
    const items = Array.from(document.getElementsByClassName(scrollItemClassName));

    const index = items.reverse().findIndex((item) => {
      if (direction === 'horizontal') {
        return item.getBoundingClientRect().left <= containerBoundingClientRect.left;
      }
      return item.getBoundingClientRect().top < containerBoundingClientRect.top;
    });

    if (index > -1) {
      items[index].scrollIntoView({ block: 'nearest', inline: 'start', behavior: 'smooth' });

      return { isLast: index === items.length - 1 };
    }
    return null;
  }
}
