import { Api, HttpService } from './http.service';
import { IInviteInfo, IUser } from './user.interface';

export class AuthService {
  constructor(private httpService: HttpService) {}

  async register(user: IUser): Promise<any> {
    try {
      const response = await this.httpService.axios.post(Api.REGISTER, user);
      return response.data as { ok: boolean };
    } catch (e) {
      if (e.response.status < 500) {
        return e.response.data as { errors: { [key in keyof IUser]: string } };
      }
    }
  }

  async resendEmail(body: { login: string; email: string }): Promise<{ ok: string; errors?: string[] }> {
    try {
      const result = await this.httpService.axios.post<{ ok: string }>(Api.RESEND_EMAIL, body);
      return result.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async login({
    login,
    password,
    device_name = 'browser'
  }: {
    login: string;
    password: string;
    device_name?: string;
  }): Promise<{ token?: string; errors?: string[] }> {
    try {
      const result = await this.httpService.axios.post<{ token: string }>(Api.LOGIN, {
        login,
        password,
        device_name
      });
      this.httpService.token = result.data.token;

      return result.data as { token: string };
    } catch (e) {
      return e.response.data as { errors: string[] };
    }
  }

  async logout() {
    const result = await this.httpService.axios.post(Api.LOGOUT, null);
    this.httpService.clearToken();
    return result;
  }

  async getUser(): Promise<IUser & { errors?: string[] }> {
    try {
      const result = await this.httpService.axios.get<{ data: IUser }>(Api.USER);
      return result.data.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async getInviteInfo(id: string, code: string): Promise<IInviteInfo> {
    try {
      const result = await this.httpService.axios.get<{ data: IInviteInfo }>(Api.INVITE_INFO, {
        params: {
          id,
          code
        }
      });
      return result.data.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async verifyEmail(id: string, hash: string): Promise<{ ok: boolean }> {
    try {
      const result = await this.httpService.axios.post<{ ok: boolean }>(`${Api.VERIFY_EMAIL}/${id}/${hash}`);
      return result.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async forgetPassword(email: string): Promise<{ ok: boolean; errors?: string[] }> {
    try {
      const result = await this.httpService.axios.post<{ ok: boolean; errors?: string[] }>(Api.FORGET_PASSWORD, { email });
      return result.data;
    } catch (e) {
      return e.response.data;
    }
  }

  async resetPassword(email: string, token: string): Promise<{ ok: boolean; password: string; errors?: string[] }> {
    try {
      const result = await this.httpService.axios.post<{ ok: boolean; password: string; errors?: string[] }>(
        `${Api.RESET_PASSWORD}/${email}/${token}`,
        null
      );
      return result.data;
    } catch (e) {
      return e.response.data;
    }
  }

  private _registrationUserData: IUser & { _accept: boolean; _confirm_password?: string } = {
    id: 0,
    public_avatar: '',
    public_family: '',
    public_name: '',
    about: '',
    address: '',
    birthday: '',
    country: '',
    email: '',
    family: '',
    login: '',
    name: '',
    password: '',
    patronymic: '',
    phone: undefined,
    scope: null,
    sex: undefined,
    work_place: '',
    work_position: '',
    worktype: null,
    _accept: false,
    invite_code: null,
    invite_id: null,
    is_public: 0,
    _confirm_password: ''
  };
  set registrationUserData(value: Partial<IUser & { _accept: boolean; _confirm_password?: string }>) {
    Object.assign(this._registrationUserData, value);
  }

  get registrationUserData(): Partial<IUser & { _accept: boolean; _confirm_password?: string }> {
    return this._registrationUserData;
  }
}
