import React, { useContext, useState } from 'react';
import './Login.scss';
import { Input } from '../../../common/Input/Input';
import { useHistory } from 'react-router-dom';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { ValidatorsTypes } from '../../../common/Validator/ValidatorsTextTemplates';
import { Button } from '../../../common/Button/Button';
import { GlobalContext } from '../../../GlobalContext';
import { Routes } from '../../../services/routes.enum';
import { PASSWORD_PATTERN } from '../../../common/Validator/validators';

export function Login() {
  const {
    services: { authService },
    spinner: { showSpinner, hideSpinner }
  } = useContext(GlobalContext);
  const [asyncErrors, setAsyncErrors] = useState<{ errors?: string[] }>();

  const validationSchema = Yup.object().shape({
    login: Yup.string().max(50, ValidatorsTypes.maxLength_50).trim().required(ValidatorsTypes.required),
    password: Yup.string()
      .min(8, ValidatorsTypes.minLength_8)
      .max(50, ValidatorsTypes.maxLength_50)
      .trim()
      .matches(PASSWORD_PATTERN, ValidatorsTypes.notValidPassword)
      .required(ValidatorsTypes.required)
  });

  const history = useHistory();

  return (
    <div className="section__login">
      <h3>Вход в профиль</h3>
      <Formik
        initialValues={{ login: '', password: '' }}
        validationSchema={validationSchema}
        validate={() => {
          return asyncErrors;
        }}
        onSubmit={async (values, actions) => {
          showSpinner();

          const result = await authService.login(values);
          if (result?.token) {
            history.push(Routes.PROFILE);
          } else if (result?.errors) {
            setAsyncErrors(result as { errors: string[] });
          }

          hideSpinner();
        }}
      >
        {({ errors, touched, values, handleSubmit, setFieldValue, isValid, validateForm, setFieldTouched }) => (
          <form
            onSubmit={(e) => {
              e.preventDefault();
              validateForm().then(() => handleSubmit());
            }}
          >
            <Input
              name="login"
              valueChange={(value) => {
                setAsyncErrors({});
                setFieldValue('login', value);
              }}
              value={values.login}
              label="Номер телефона или e-mail"
              placeholder="Адрес электронной почты или номер телефона"
              description="Логин"
              onTouch={() => {
                setFieldTouched('login');
              }}
              error={errors?.login && touched?.login && errors.login}
            />

            <div className="form-group">
              <Input
                name="password"
                type="password"
                valueChange={(value) => {
                  setAsyncErrors({});
                  setFieldValue('password', value);
                }}
                value={values.password}
                label="Пароль"
                placeholder="Введите ваш пароль"
                description="Пароль"
                onTouch={() => setFieldTouched('password')}
                error={errors?.password && touched?.password && errors.password}
              />
              <span className="highlight">{asyncErrors?.errors?.join()}</span>
            </div>

            <div className="form-group form-actions">
              <Button primary={true} onClick={() => validateForm().then(() => handleSubmit())} type="submit">
                Войти
              </Button>
              <span
                onClick={() => {
                  history.push(Routes.FORGET_PASSWORD);
                }}
              >
                Забыли пароль?{' '}
              </span>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
}
