import React, { useState } from 'react';
import './TextArea.scss';
import { FormError } from '../Validator/FormError/FormError';

type ITextAreaProps = {
  value?: string;
  valueChange: (value: string) => void;
  name: string;
  label?: string;
  placeholder?: string;
  description?: string;
  rows?: number;
  error?: string;
  onTouch?: () => void;
  bgColor?: 'white' | 'gray';
  disabled?: boolean;
};

export function TextArea({
  value = '',
  valueChange,
  name,
  label,
  placeholder,
  description,
  rows = 6,
  error,
  onTouch,
  bgColor = 'gray',
  disabled = false
}: ITextAreaProps) {
  const [inputValue, setValue] = useState<string>(value);

  const handleChange = (event) => {
    onTouch && onTouch();
    valueChange(event.target.value);
    setValue(event.target.value);
  };

  const onBlur = () => {
    onTouch && onTouch();
  };

  const className = `form-group ${inputValue ? 'filled' : ''} ${error ? 'error' : ''}`;

  return (
    <div className={className}>
      <label htmlFor={name}>{placeholder ? (value ? label : '') : label}</label>
      <textarea
        className={bgColor}
        id={name}
        name={name}
        rows={rows}
        placeholder={placeholder}
        value={inputValue}
        onChange={handleChange}
        onBlur={onBlur}
        disabled={disabled}
      />
      <div className="description">{error ? <FormError>{error}</FormError> : !value && description}</div>
    </div>
  );
}
