import React, { useRef } from 'react';
import style from './Slider.module.scss';
import { Icon } from '../Icon/Icon';

type ISliderProps = {
  slidesOnPage?: number;
  children: any;
  controlsVerticalOffset?: number;
  controlsHorizontalOffset?: number;
  showScroll?: boolean;
  controlsHorizontalPosition?: 'right' | 'left';
  controlsVerticalPosition?: 'top' | 'bottom';
};

export function Slider({
  children,
  slidesOnPage = 2,
  controlsVerticalOffset = 20,
  controlsHorizontalOffset = 20,
  showScroll = false,
  controlsHorizontalPosition = 'right',
  controlsVerticalPosition = 'top'
}: ISliderProps) {
  const ref = useRef();
  const width = 100 / slidesOnPage;

  const handleNext = () => {
    const container = ref?.current || null;
    if (!container) {
      return;
    }

    const slider = container;
    const sliderBoundingClientRect = slider?.getBoundingClientRect();
    const slides = Array.from(container.getElementsByClassName(style.Slide)) as HTMLDivElement[];

    const element = slides.find((slide) => {
      const slideBoundingClientRect = slide.getBoundingClientRect();
      return (slideBoundingClientRect.right + slideBoundingClientRect.left) / 2 > sliderBoundingClientRect.right;
    });
    if (element) {
      element.scrollIntoView({ inline: 'end', block: 'nearest' });
    }
  };
  const handlePrev = () => {
    const container = ref?.current || null;
    if (!container) {
      return;
    }

    const slider = container;
    const sliderBoundingClientRect = slider.getBoundingClientRect();
    const slides = Array.from(container.getElementsByClassName(style.Slide)) as HTMLDivElement[];

    const element = slides.reverse().find((slide) => {
      const slideBoundingClientRect = slide.getBoundingClientRect();

      return (slideBoundingClientRect.left + slideBoundingClientRect.right) / 2 <= sliderBoundingClientRect.left;
    });
    if (element) {
      element.scrollIntoView({ block: 'nearest', inline: 'start' });
    }
  };
  return (
    <div className={`${style.Slider}`} ref={ref}>
      <div className={`${style.container} ${showScroll ? undefined : style.noScroll}`}>
        {children.map((child, index) => (
          <Slide key={index} width={width + '%'}>
            {child}
          </Slide>
        ))}
      </div>

      <div
        className={style.controls}
        style={{
          [controlsVerticalPosition]: 0,
          [controlsHorizontalPosition]: 0,
          transform: `translate(${controlsHorizontalOffset}px, ${controlsVerticalOffset}px)`
        }}
      >
        <div onClick={handlePrev}>
          <Icon iconName="arrowLeft" />
        </div>
        <div onClick={handleNext}>
          <Icon iconName="arrowRight" />
        </div>
      </div>
    </div>
  );
}

type ISlideProps = {
  children: any;
  width: string;
};

function Slide({ children, width }: ISlideProps) {
  return (
    <div className={style.Slide} style={{ flex: `0 0 ${width}` }}>
      {children}
    </div>
  );
}
