import React from 'react';
import styles from './Radio.module.scss';
import { FormError } from '../Validator/FormError/FormError';

type IRadioProps = {
  value?: number | string;
  valueChange?: (value: number | string) => void;
  name?: string;
  label?: string;
  description?: string;
  options: object[];
  valueKey?: string;
  labelKey?: string;
  error?: string;
  onTouch?: () => void;
  children?: any;
  required?: boolean;
};

export function Radio({
  options,
  value,
  valueChange,
  label = '',
  description = '',
  name,
  valueKey = 'id',
  labelKey = 'label',
  error,
  onTouch,
  children,
  required = false
}: IRadioProps) {
  const handleChange = (value: string | number) => {
    onTouch && onTouch();
    valueChange(value);
  };

  return (
    <div className={`form-group ${styles.formGroup} ${error ? styles.error : ''}`}>
      <label htmlFor={name}>{label + (label && required ? ' *' : '')}</label>
      <div className={`${styles.formGroupRadio} form-group__radio`}>
        {options.map((option) => (
          <label key={option[valueKey]}>
            <input
              type="radio"
              name={name}
              value={option[valueKey]}
              onChange={handleChange.bind(this, option[valueKey])}
              checked={option[valueKey] === value}
            />
            <span dangerouslySetInnerHTML={{ __html: option[labelKey] }} />
          </label>
        ))}
      </div>
      <div className="description">{error ? <FormError>{error}</FormError> : !value && description}</div>

      {children && children}
    </div>
  );
}
