import React, { useState } from 'react';
import styles from './UserAgreement.module.scss';

export function UserAgreement() {
  const [expanded, setExpandedStatus] = useState<boolean>(false);

  const toggle = (
    <>
      <span>{expanded ? ' ' : '... '} </span>
      <span className={`${styles.toggle} highlight`} onClick={setExpandedStatus.bind(null, !expanded)}>
        {expanded ? 'скрыть' : 'читать больше'}
      </span>
    </>
  );

  const expandedContent = (
    <>
      <br />
      <div className={`${styles.card} bg-grey-1 with-radius`}>
        <ul className=" large text-uppercase">
          <li>все доступы к платформе вне Беларуси</li>
          <li>система доверительных контактов</li>
          <li>шифрование данных и трафика</li>
          <li>анонимизация каждого пользователя</li>
        </ul>
      </div>
      <br />

      <p>Никто физически не может добраться до информации о вас, потому что:</p>
      <ul>
        <li>Сервера платформы находятся вне Беларуси.</li>
        <li>Доступ к домену, хостингу и Production вне Беларуси.</li>
      </ul>
      <br />

      <p>Никто не может перехватить данные о вас, пока вы отправляете их нам:</p>
      <ul>
        <li>Данные шифруются перед отправкой (ключ шифрования недоступен).</li>
        <li>Трафик между сервером и браузером шифруется.</li>
      </ul>
      <br />

      <p>Никто не сможет добыть информацию о вас, перехватив базу данных:</p>
      <ul>
        <li>В системе вы будете как неопознанный зубр или лось (каждому человеку рандомно присваивается имя).</li>
        <li>
          Вся информация о вас шифруется и недоступна абсолютно никому до момента, пока вы не поставите в предложенном окне галку “раскрыть
          мои данные”.
        </li>
      </ul>
      <br />

      <p>
        На платформу попадают лишь доверенные люди - те, за кого поручился кто-то. Если вдруг внутри оказывается нехороший человек, можно
        отследить, кто пригласил его в систему и увидеть целую ветку неблагонадежных людей.
      </p>
      <br />

      <p>Плохой человек при попадании внутрь не сможет никого вычислить:</p>
      <ul>
        <li>Внутри платформы он видит только зубров и лосей, но не знает даже близко с кем общается.</li>
        <li>Мы используем безопасные каналы связи и коммуникации - Telegram.</li>
        <li>
          Мы используем Incognito для общения в чатах платформы (не вы сидите в чате, а ваш бот, и поэтому никто не знает ваше имя в
          Telegram)
        </li>
      </ul>

      <p>Если кто-то захватил ваш телефон или компьютер, он не сможет войти в ваш аккаунт:</p>
      <ul>
        <li>
          Ваш профиль могут оперативно заблокировать ваши доверенные лица
          {expanded && toggle}
        </li>
      </ul>
      <br />
    </>
  );

  return (
    <div className={`${styles.UserAgreement}`}>
      <div className={`${styles.content} ${expanded ? styles.expanded : null}`}>
        <p>
          Ведите данные для регистрации. Все данные будут надежно зашифрованы, никто не сможет получить к ним доступ, пока вы сами не дадите
          разрешение на открытие своих данных
          {!expanded && toggle}
        </p>
        {expanded ? expandedContent : null}
      </div>
    </div>
  );
}
