import React, { useEffect } from 'react';
import styles from './SuggestionForm.module.scss';
import { Icon } from '../../../common/Icon/Icon';
import { Button } from '../../../common/Button/Button';

type ISuggestionFormProps = {
  setSuggestionBlockStatus: (...params: any[]) => void;
};

export function SuggestionForm({ setSuggestionBlockStatus }: ISuggestionFormProps) {
  useEffect(() => {
    const element = document.getElementById('suggestion-form');
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }, []);

  return (
    <div id="suggestion-form" className={styles.SuggestionForm}>
      <div className={`${styles.close} p`} onClick={setSuggestionBlockStatus.bind(null, false)}>
        <Icon iconName="close" />
      </div>
      <div className={styles.container}>
        <h2 className={`${styles.text}`}>Модуль "Предложения" находится на этапе разработки</h2>
      </div>
      <div className={`${styles.backButton} p-top p-0-top-lg`}>
        <Button primary={true}>наша идея / миссия</Button>
      </div>
      <div className={`${styles.info} p-top p-0-top-lg`}>
        <p>
          Здесь вы можете рассказать о проблеме и предложить ее решение. В этом же блоке можно и нужно сообщить о правонарушении. Тогда в
          работу включится юрист. Рациональные предложения и любая инициатива приветствуется! Будет ли ваше предложение взято в работу -
          решит честное и прозрачное голосование. Поддержите голосом инициативы коллег, если считаете их важными и нужными.
        </p>
      </div>
    </div>
  );
}
