import React, { useState } from 'react';
import styles from './TopSuggestions.module.scss';
import { suggestions } from '../../../constans/suggestions-list';
import { Icon } from '../../../common/Icon/Icon';
import { Button } from '../../../common/Button/Button';

type ITopSuggestionsProps = {
  setSuggestionBlockStatus: (status: boolean) => void;
};

export function TopSuggestions({ setSuggestionBlockStatus }: ITopSuggestionsProps) {
  const [suggestionsList, setSuggestionsList] = useState<typeof suggestions>(suggestions);
  const [hoveredSuggestion, setHoveredSuggestion] = useState<typeof suggestions[0]>();

  const handleLike = (suggestion: typeof suggestions[0]) => {
    const index = suggestionsList.findIndex((s) => s.id === suggestion.id);
    if (index > -1) {
      suggestionsList[index].liked = !suggestionsList[index].liked;
      setSuggestionsList([...suggestionsList]);
    }
  };

  let timeoutId;
  const handleMouseEnter = (suggestion) => {
    setHoveredSuggestion(suggestion);
    if (timeoutId) {
      clearTimeout(timeoutId);
      timeoutId = null;
    }
  };
  const handleMouseOut = () => {
    timeoutId = setTimeout(() => {
      setHoveredSuggestion(null);
    }, 500);
  };

  return (
    <div className={styles.TopSuggestions}>
      <div className={`${styles.suggestionsList} p`}>
        {suggestionsList.map((suggestion) => (
          <div className={styles.suggestion} key={suggestion.id}>
            <div className={styles.icon} onClick={handleLike.bind(null, suggestion)}>
              <Icon iconName="like" color={suggestion.liked ? 'primary' : 'disabled'} width={20} />
            </div>
            <div className={styles.likesCount}>
              <span>{suggestion.likesCount}</span>
            </div>
            <div
              className={styles.suggestionText}
              onMouseEnter={handleMouseEnter.bind(null, suggestion)}
              onMouseOut={handleMouseOut.bind(null, null)}
            >
              {suggestion.label}
            </div>
          </div>
        ))}
      </div>
      <div className={`${styles.rightContainer} p`}>
        <div className={styles.rightContainerContent}>
          <div className={`${hoveredSuggestion ? '' : styles.collapsed} ${styles.suggestionInfo}`}>{hoveredSuggestion?.label}</div>
          <div className={`${hoveredSuggestion ? styles.collapsed : ''} ${styles.suggest}`}>
            <h3>Проявите инициативу, предложите своей организации что-то, что продвинет всех вас вперед к целям группы и общим целям</h3>
            <Button icon="bell" primary={true} onClick={setSuggestionBlockStatus.bind(null, true)}>
              Предложить
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
