import React from 'react';
import styles from './CloseButton.module.scss';
import { Icon } from '../Icon/Icon';
import { useHistory } from 'react-router-dom';
import { Routes } from '../../services/routes.enum';

type ICloseButtonProps = {
  targetRoute?: Routes;
};

export function CloseButton({ targetRoute }: ICloseButtonProps) {
  const history = useHistory();

  const backHandle = () => {
    if (targetRoute) {
      history.push(targetRoute + history.location.search);
    } else {
      history.goBack();
    }
  };
  return (
    <div className={styles.CloseButton} onClick={backHandle}>
      <Icon iconName="close" />
    </div>
  );
}
