import React from 'react';
import styles from './Footer.module.scss';
import { Icon } from '../../common/Icon/Icon';
import { Routes } from '../../services/routes.enum';
import { useHistory } from 'react-router-dom';

type IFooterProps = {
  children?: React.ReactElement | React.ReactElement[];
  showLinks?: boolean;
  showLogoMobile?: boolean;
  showLogoDesktop?: boolean;
};

export function Footer({ children, showLinks = true, showLogoMobile = true, showLogoDesktop = true }: IFooterProps) {
  const history = useHistory();

  const navigateTo = (route: Routes) => {
    history.push(route);
  };

  return (
    <footer className={`${styles.Footer}`}>
      {children ? (
        <div className={styles.actionsContainer}>{children}</div>
      ) : (
        <div className={styles.footerLogo}>
          {showLogoDesktop && (
            <div className={styles.logo}>
              <Icon iconName="logo" />
            </div>
          )}
          {showLogoMobile && (
            <div className={styles.logoSmall}>
              <Icon iconName="logoSmall" />
            </div>
          )}
        </div>
      )}

      <div className={styles.footerLinks}>
        {showLinks && (
          <>
            <a onClick={navigateTo.bind(null, Routes.RULES_SERVICE)}>Правила сервиса</a>
            <a onClick={navigateTo.bind(null, Routes.PRIVACY_POLICY)}>Политика конфиденциальности</a>
          </>
        )}
        <p>© 2021. freeunion.online</p>
      </div>
    </footer>
  );
}
