import React, { useRef, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { HttpService } from './services/http.service';
import { DictionariesService } from './services/dictionaries.service';
import { AuthService } from './services/auth.service';
import {
  authService,
  deviceService,
  dictionariesService,
  httpService,
  inviteLinksService,
  organisationsService,
  scrollService,
  suggestionsService,
  userService
} from './services';
import { InviteLinksService } from './services/invite-links.service';
import { IUser } from './services/user.interface';
import { UserService } from './services/user.service';
import { OrganisationsService } from './services/organisations.service';
import { IOrganisation, IOrganisationShort } from './services/organisation.interface';
import { SuggestionsService } from './services/suggestions.service';
import { DeviceService } from './services/device.service';
import { ScrollService } from './services/scroll.service';

type IModalOptions = {
  isOpen: boolean;
  params: {
    topbarLeftContainer?: React.ReactElement;
    topbarRightContainer?: React.ReactElement;
    mainContainer?: React.ReactElement;
  };
};

type IGlobalState = {
  services: {
    httpService: HttpService;
    dictionariesService: DictionariesService;
    authService: AuthService;
    inviteLinksService: InviteLinksService;
    userService: UserService;
    organisationsService: OrganisationsService;
    suggestionsService: SuggestionsService;
    deviceService: DeviceService;
    scrollService: ScrollService;
  };

  user?: IUser;
  setUser: (user: IUser) => void;

  organisations: IOrganisationShort[];
  setOrganisations: (organisations: IOrganisationShort[]) => void;

  selectedOrganisationId: number;
  selectedOrganisation: IOrganisation;
  selectOrganisation: (organisationId: number, organisation?: IOrganisation) => void;

  isSidebarExpanded: boolean;
  setSidebarExpandedState: (status: boolean) => void;

  globalModalOptions: IModalOptions;
  openModal: (options?: Pick<IModalOptions, 'params'>) => void;
  closeModal: () => void;
  applyToOrganizationIconRef: any;
  spinner: {
    isSpinnerOpened: boolean;
    showSpinner: () => void;
    hideSpinner: () => void;
  };
};

export const GlobalContext = React.createContext<IGlobalState>(null);

export const GlobalProvider = ({ children }: { children: any }) => {
  const [user, setUser] = useState<IUser>(null);
  const [organisations, setOrganisations] = useState<IOrganisationShort[]>(null);
  const [selectedOrganisation, setSelectedOrganisationState] = useState<IOrganisation>(null);
  const [isSidebarExpanded, setSidebarExpandedState] = useState<boolean>();
  const [isSpinnerOpened, setIsSpinnerOpenedStatus] = useState<boolean>(false);

  const globalModalOptions = {
    isOpen: false,
    params: {
      mainContainer: null,
      topbarLeftContainer: null,
      topbarRightContainer: null
    }
  };
  const [globalModalState, setGlobalModalOptions] = useState<IModalOptions>(globalModalOptions);
  const openModal = (options) => {
    globalModalOptions.isOpen = true;
    globalModalOptions.params = {
      topbarLeftContainer: null,
      topbarRightContainer: null,
      mainContainer: null,
      ...options.params
    };
    setGlobalModalOptions({ ...globalModalOptions });
  };
  const closeModal = () => {
    globalModalOptions.isOpen = false;
    globalModalOptions.params = {
      mainContainer: null,
      topbarLeftContainer: null,
      topbarRightContainer: null
    };
    setGlobalModalOptions(globalModalOptions);
  };

  const history = useHistory();

  const selectOrganisation = async (organisationId: number, organisation?: IOrganisation) => {
    console.log(organisationId);
    setIsSpinnerOpenedStatus(true);
    const searchParams = selectedOrganisation ? `?organisation=${selectedOrganisation.id}` : '';
    history.push(`${history.location.pathname}${searchParams}`);

    if (organisation) {
      setSelectedOrganisationState(organisation);
      return;
    }

    const loadedOrganisation = await organisationsService.getOrganisationById(organisationId);
    setSelectedOrganisationState(loadedOrganisation);
    setIsSpinnerOpenedStatus(false);
  };

  const state: IGlobalState = {
    user,
    setUser,

    organisations,
    setOrganisations,
    selectedOrganisationId: selectedOrganisation?.id || null,
    selectedOrganisation,
    selectOrganisation,

    isSidebarExpanded,
    setSidebarExpandedState,
    globalModalOptions: globalModalState,
    openModal,
    closeModal,
    applyToOrganizationIconRef: useRef(),
    services: {
      httpService,
      authService,
      dictionariesService,
      inviteLinksService,
      userService,
      organisationsService,
      suggestionsService,
      deviceService,
      scrollService
    },
    spinner: {
      isSpinnerOpened,
      showSpinner: () => setIsSpinnerOpenedStatus(true),
      hideSpinner: () => setIsSpinnerOpenedStatus(false)
    }
  };

  return <GlobalContext.Provider value={state}>{children}</GlobalContext.Provider>;
};
