import React, { useState } from 'react';
import styles from './Tabs.module.scss';
import { ITabItemProps } from './TabItem';

type ITabsProps = {
  children: React.ReactElement<ITabItemProps>[];
};

export function Tabs({ children }: ITabsProps) {
  const [activeTab, setActiveTab] = useState<React.ReactElement>(children[0]);

  return (
    <div className={styles.Tabs}>
      <div>
        <ul>
          {children.map((child) => (
            <li
              key={child.props.label}
              className={`${activeTab.props.label === child.props.label ? styles.active : ''} ${
                child.props.disabled ? styles.disabled : ''
              }`}
              onClick={!child.props.disabled ? setActiveTab.bind(null, child) : undefined}
            >
              {child.props.label}
            </li>
          ))}
        </ul>
      </div>
      <div className={styles.activeTab}>{activeTab}</div>
    </div>
  );
}
