import React, { useContext, useState } from 'react';
import styles from './ForgetPassword.module.scss';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { ValidatorsTypes } from '../../../common/Validator/ValidatorsTextTemplates';
import { GlobalContext } from '../../../GlobalContext';
import { Input } from '../../../common/Input/Input';
import { Button } from '../../../common/Button/Button';

export function ForgetPassword() {
  const {
    services: { authService },
    spinner: { showSpinner, hideSpinner }
  } = useContext(GlobalContext);

  const validationSchema = Yup.object().shape({
    email: Yup.string().trim().email(ValidatorsTypes.invalidEmail).max(100).required(ValidatorsTypes.required)
  });

  const [requestSentSuccessfully, setRequestSentSuccessfullyStatus] = useState<boolean>(false);

  const { errors, touched, values, handleSubmit, setFieldValue, validateForm, setFieldTouched } = useFormik({
    initialValues: { email: '' },
    validationSchema,
    onSubmit: async (values, actions) => {
      showSpinner();
      const result = await authService.forgetPassword(values.email);
      if (result.ok) {
        setRequestSentSuccessfullyStatus(true);
      } else {
        actions.setErrors({
          email: result.errors.join()
        });
      }
      hideSpinner();
    }
  });

  return (
    <div className={styles.ForgetPassword}>
      <h3>смена пароля</h3>
      <Input
        name="email"
        value={values.email}
        valueChange={(value) => {
          setFieldValue('email', value, true);
        }}
        label="E-mail"
        type="email"
        description="Адрес электронной почты"
        placeholder="Введите адрес электронной почты*"
        onTouch={() => {
          setFieldTouched('email');
        }}
        error={errors?.email && touched?.email && errors.email}
        required={true}
      />

      <div className={styles.actions}>
        <Button primary={true} onClick={() => validateForm().then(() => handleSubmit())} type="submit">
          Отправить
        </Button>
      </div>
      <br />
      {requestSentSuccessfully && <p className={styles.successText}>Запрос был успешно отправлен</p>}
    </div>
  );
}
